import React from 'react';
import { Row, Col } from "antd";
import SyntaxHighlighter from 'react-syntax-highlighter';
import { darcula } from 'react-syntax-highlighter/dist/esm/styles/hljs';

export function CodeExamplePage() {
  return (
    <div className="codeexamples-wrapper">
      <Row gutter={16} justify="center">
          <Col span={12}>
              <h2>Getting Started</h2>
              <p>The following code sets up connection with your MIDI keyboard and listens to all the notes.</p>
            </Col>
            <Col span={12}>
                <SyntaxHighlighter language="typescript" style={darcula}>{`
import { MIDIVal } from "@midival/core";

MIDIVal.connect()
.then(access => {
  const input = new MIDIValInput(access.inputs[0]);
  input.onAllNoteOn(event => {
    console.log("Note on", event.note, event.velocity);
  });
});`}
                </SyntaxHighlighter>
            </Col>
        </Row>
        <Row gutter={16} justify="center">
          <Col span={12}>
              <h2>Sending Messages to your synth</h2>
              <p>MIDIVal can also be used to control your synthesiser.</p>
            </Col>
            <Col span={12}>
                <SyntaxHighlighter language="typescript" style={darcula}>{`
import { MIDIVal } from "@midival/core";

MIDIVal.connect()
.then(access => {
  const output = new MIDIValOutput(access.outputs[0]);
  output.sendNoteOn(64, 127);
  output.sendControlChange(5, 50); // increasing portamento time.
});`}
                </SyntaxHighlighter>
            </Col>
        </Row>
        <Row gutter={16} justify="center">
          <Col span={12}>
              <h2>Using MIDIVal with Node and React Native</h2>
              <p>MIDIVal is compatible with modern browsers, Node.js and React Native. To configure it, simply provide corresponding adapter. The rest of the code stays the same between environments!</p>
            </Col>
            <Col span={12}>
                <SyntaxHighlighter language="typescript" style={darcula}>{`
// For Node.js
import { MIDIVal } from "@midival/core";
import { NodeMIDIAccess } from "@midival/node";
import midi from "midi";

MIDIVal.configureAccessObject(new NodeMIDIAccess(midi));`}
                </SyntaxHighlighter>
                <SyntaxHighlighter language="typescript" style={darcula}>{`
// For React Native
import { MIDIVal } from "@midival/core";
import { ReactNativeMIDIAccess } from "@midival/react-native";

MIDIVal.configureAccessObject(new ReactNativeMIDIAccess());`}
                </SyntaxHighlighter>
            </Col>
        </Row>
    </div>
  );
}